import React, { useState, useRef, useEffect } from 'react'
import './index.scss'
import Display from './Display'
import Keypad from './Keypad'
import Button from './Button'
import { useDimension, useOutside } from 'Page/StockEntry/GeneralServices'
// import {} from './services'

const Calculator = (props) => {
    const [data, setData] = useState('')
    const wrapperRef = useRef(null);

    const calculate = (onChange) => {
        try {
            const result = eval(data);
            const value = {
                value: result?.toString(),
                name: props.name
            }

            onChange(value)
            // setData(result);
        } catch (e) {
            console.log(e);
            setData('')
        }
    }

    useOutside({ wrapperRef, setShow: props.setShowCalculator, show: props.showCalculator });

    const { isMobile } = useDimension()
    // track element position
    const elem = document?.getElementById(`dropdown${props.entryListIdx}${props.poListIdx}`);
    let position = elem?.getBoundingClientRect()
    let menuOnTop = position?.bottom > 600 ? true : false
    if (isMobile) menuOnTop = position?.bottom > 400 ? true : false

    useEffect(() => {
        position = elem?.getBoundingClientRect()
    }, [props.showCalculator])

    const top = position?.top - (menuOnTop ? 400 : (isMobile ? 50 : -30))
    const handleClick = e => {
        const value = e.target.getAttribute('data-value');
        switch (value) {
            case 'clear':
                props.onChange({
                    value: '',
                    name: props.name
                })
                break;
            case 'clearall':
                props.onChange({
                    value: '',
                    name: props.name
                })
                setData('');
                break;
            case 'equal':
                calculate(props.onChange);
                break;
            default:
                setData(data + value);
        }
    }
    return (
        <div className={`${props.useCalculator ? null : 'd-none'} calculator ${props.className}`} ref={wrapperRef} style={{ top, left: isMobile ? 0 : false, width: isMobile ? '100%' : false }}>
            <Display data={data} setData={setData} />
            <Keypad>
                <Button onClick={handleClick} label="x" value="*" />
                <Button onClick={handleClick} label="7" value="7" />
                <Button onClick={handleClick} label="4" value="4" />
                <Button onClick={handleClick} label="1" value="1" />
                <Button onClick={handleClick} label="0" value="0" />

                <Button onClick={handleClick} label="/" value="/" />
                <Button onClick={handleClick} label="8" value="8" />
                <Button onClick={handleClick} label="5" value="5" />
                <Button onClick={handleClick} label="2" value="2" />
                <Button onClick={handleClick} label="." value="." />

                <Button onClick={handleClick} label="+" value="+" />
                <Button onClick={handleClick} label="9" value="9" />
                <Button onClick={handleClick} label="6" value="6" />
                <Button onClick={handleClick} label="3" value="3" />
                <Button onClick={handleClick} label="=" value="equal" />

                <Button onClick={handleClick} label="-" value="-" />
                <Button onClick={handleClick} label="CE" size='2' value="clear" />
                <Button onClick={handleClick} label="C" size='2' value="clearall" />
            </Keypad>
        </div>
    )
}
export default Calculator